.spoiler{
    position: absolute;
   right: 20px;
   top: 8px;
   cursor: pointer;
  
   
}
.spoiler:after,
.spoiler:before{
    content: '';
    position: absolute;
    width: 18px;
    height: 3px;
    right: 10px;
    top: 16px;
    background-color: rgb(247, 244, 240);
    transition: all 0.5s ease 0s;
}

.spoiler:after{
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);

}
.spoiler:before{
    margin-right: 12.5px;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);

}
.togeleClassSpoiler:after,
.togeleClassSpoiler:before{
    content: '';
    position: absolute;
    width: 18px;
    height: 3px;
    right: 10px;
    top: 16px;
    background-color: rgb(247, 244, 240);
    transition: all 0.5s ease 0s;

}
.togeleClassSpoiler{
    
}
.togeleClassSpoiler:after{
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);

}
.togeleClassSpoiler:before{
    margin-right: 12.5px;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
